@import "./mixins";

@font-face {
  font-family: 'IBMPlexSansKR-Light';
  src: url('./resources/fonts/IBMPlexSansKR-Light/IBMPlexSansKR-Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'IBMPlexSansKR-Light', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  min-height: 100vh;
  display: flex;
  // overflow-y: overlay;
}

#root{
  display: flex;
  flex-direction: column;
  flex: 1 auto;
  white-space: pre-line;
}

body.jth-menuList-show{
  overflow-y: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a{
  text-decoration: none;
  position: relative;
  color: var(--color-base);
  width: fit-content;
  height: fit-content;
  font-weight: bold;
  z-index: 1;

  &:before{
    content: '';
    width: 100%;
    height: 0.15rem;
    position: absolute;
    bottom: 0;
    right: 0;
    transition: 0.4s;
    background-color: var(--primary-brand-base);
    z-index: -1;
  }

  &:hover:before{
    height: 100%;
    background-color: var(--background-base);
    border-radius: 0.25rem;
  }
}

figure{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  img{
    width: 100%
  }

  figcaption{
    color: var(--color-down);
    margin-top: 0.5rem;
  }
}

.jth-container{
  max-width: $maxWidth;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 5rem;
  margin: auto;
}

.jth-text{
  padding: 0.1em 0.3em;
  border-radius: 0.3em;
  background: var(--background-code-base);
  color: var(--color-code-base);
}

.jth-router-link{
  color: var(--color-down);

  &:before{
    content: none;
  }
}

.jth-svg-fill{
  fill: var(--color-base);
}

.jth-section{
  width: 100%;
  padding: 5rem 0;

  .jth-section-rowGrid{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    justify-self: self-start;
  }

  .jth-section-rowGrid-center{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    align-items: center;
    justify-items: center;
  }

  .jth-section-maxWidth-70{
    grid-template-columns: minmax(0, 70%);
  }

  .jth-section-twoColGrid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    align-items: start;
    justify-items: center;
  }

  .jth-section-twoColGrid-center{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    align-items: center;
    justify-items: center;
  }

  img{
    width: 100%;
  }

  .jth-section-getInTouch{
    display: grid;
    grid-template-columns: 1fr;
    gap: 3rem;
    align-items: center;
    justify-items: center;
    text-align: center;
  }
}

@include notTouchDevice{

  body,#root{
    &::-webkit-scrollbar{
      width: 0.5rem;
    }
    &::-webkit-scrollbar-thumb{
      background-color: var(--scrollbar-base-opacity);
    }
    &::-webkit-scrollbar-track{
      background-color: rgba(0,0,0,0);
    }
  }
}

@include mobile {

  .jth-container{
    padding: 0 1.25rem;
  }

  .jth-section{
    padding: 3rem 0;
  
    .jth-section-maxWidth-70{
      grid-template-columns: minmax(0, 100%);
    }

    .jth-section-twoColGrid{
      grid-template-columns: 1fr;
    }
  
    .jth-section-twoColGrid-center{
      grid-template-columns: 1fr;
    }

    .jth-section-mobileOrder-2{
      order: 2;
    }
  }
}

@include tablet {

  .jth-container{
    padding: 0 3rem;
  }
}

:root {
  --background-base: #FFFFFF;
  --background-base-opacity: rgba(255, 255, 255, 0.85);
  --scrollbar-base-opacity: rgba(0, 0, 0, 0.5);
  --background-code-base: #f9f2f4;
  --color-base: #000000;
  --color-down: #70757a;
  --color-code-base: #9a354a;
  --primary-brand-base: #F6A54C;
  --secondary-brand-base: #614CF6;
  --primary-brand-background-base: #504646;
  --border-base: #D6D6D6;
  --group-base: #F4F4F4;
  --danger: #E03434;
  --black: #000000;
  --white: #FFFFFF;
  
  background-color: var(--background-base);
  color: var(--color-base);

  *{
    &::selection{
      background: var(--color-base);
      color: var(--background-base);
    }
  }
}

@include darkMode{
  --background-base: #000000;
  --background-base-opacity: rgba(0, 0, 0, 0.85);
  --scrollbar-base-opacity: rgba(255, 255, 255, 0.5);
  --background-code-base: #3C3636;
  --color-base: #FFFFFF;
  --color-down: #ABABAB;
  --color-code-base: #FFB3C2;
  --border-base: #D6D6D6;
  --group-base: #5D5D5D;

  background-color: var(--background-base);
  color: var(--color-base);

  *{
    &::selection{
      background: var(--color-base);
      color: var(--background-base);
    }
  }
}

.jth-animation{
  opacity: 0;
}

.jth-animation-opacityUp-show {
  animation: opacityUp 0.5s ease-out forwards;
}

.jth-animation-opacityScale-show{
  animation: opacityScale 0.5s ease-out forwards;
}

@keyframes opacityUp {
  0%{
    opacity: 0;
    transform: translateY(2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes opacityScale {
  0%{
    opacity: 0;
    transform: translateY(2rem) scale(0.9);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}