@import "../../../mixins";

.jth-portfolios{
  padding: 3rem 0;

  .jth-portfolios-search{
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 0.063rem solid $color;

    &:hover{
      border-bottom: 0.063rem solid $primaryBrandColor;
    }

    label{
      width: 100%;
    }

    svg{
      display: flex;
      justify-content: center;
      align-items: center;
      user-select: none;
      margin: 0.5rem;
      font-size: 1.25rem;
    }
    
    .jth-portfolios-search-input{
      outline: 0;
      border: 0;
      width: 100%;
      min-height: 1.375rem;
      padding: 0.25rem 0.375rem;
      background-color: transparent;
      color: $color;
      font-size: 1rem;
      line-height: 1rem;
    }

    .jth-portfolios-search-clearIcon{
      cursor: pointer;

      &:hover{
        color: $primaryBrandColor;
      }
    }
  }

  .jth-portfolios-searchResult{
    display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
    justify-content: center;
    align-items: center;
  }

  .jth-portfolios-loading{
    margin-top: 5rem;
    font-size: 2rem;
    justify-content: center;
    align-items: center;
  }

  .jth-portfolios-list{
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    gap: 5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    list-style: none;

    li{
      width: 100%;

      .jth-portfolios-item{
        width: 100%;
        color: $color;
        display: grid;
        grid-template-columns: 1fr;
        gap: 1rem;

        .jth-portfolios-item-info{
          display: flex;
          flex-direction: row;
          align-items: center;
          flex-wrap: wrap;

          a{

            h1{
              color: $color;
            }
          }

          .jth-portfolios-item-info-links{
            display: flex;
            align-items: center;
            justify-content: flex-start;

            a{
              display: flex;
              align-items: center;

              &:before{
                content: none;
              }
  
              .jth-portfolios-item-info-link{
                font-size: 1.6rem;
              }
  
              .jth-portfolios-item-info-play{
                font-size: 1.6rem;
              }
  
              svg{
                font-size: 2rem;
                margin: 0.5rem;
              }
            }
          }
        }

        img{
          width: 100%;
          object-fit: cover;
          object-position: center center;
        }
      }
    }
  }

  .jth-portfolios-nothing{
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    align-items: center;
    justify-items: center;
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}

@include mobile{
  .jth-portfolios{
    // margin-top: 3rem;
  
    .jth-portfolios-list{
  
      li{
  
        .jth-portfolios-item{
  
          .jth-portfolios-item-info{
  
            .jth-portfolios-item-info-links{
              width: 100%;
              margin: auto;
  
              a{

                &:nth-child(1){

                  svg{
                    margin-left: 0;
                  }
                }

                &:nth-last-child(1){

                  svg{
                    margin-right: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
